
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import Pair from 'common/Pair.vue'
import { HistoryOrder as HistoryOrderInterface } from '@/modules/store/data/resources'
import { missingTranslationLogger } from '@/modules/orders/helpers/missing-translation-logger'

@Component({
  name: 'HistoryOrderDetailsRefund',
  components: {
    Pair
  }
})
export default class HistoryOrderDetailsRefund extends Vue {
  @Prop() details!: HistoryOrderInterface

  get incidentReason(): string {
    return this.details
      .refundIncident!.reason.split(',')
      .map(reason => this.getRefundReason(reason))
      .join(', ')
  }

  getRefundReason(reason: string) {
    const reasonTranslationKey = `order.refund.reason.${reason}`
    const translationForReasonExists = this.$te(reasonTranslationKey)
    if (translationForReasonExists) {
      return this.$t(reasonTranslationKey)
    }
    missingTranslationLogger.logMissingTranslationOnce(
      reasonTranslationKey,
      `Translation of refund reason is missing (key = ${reasonTranslationKey}) (locale = ${this.$i18n.locale})`
    )
    return this.$t('order.refund.reason.OTHER')
  }
}
